import * as actionTypes from '../actions/actionTypes'
import {updateObject} from '../../shared/utility'

const initialState = {
    orders:[],
    loading:false,
    purchased:false
}


    const purchseBurgerInit = (state,action)=>{
        return updateObject(state,{purchased:false})
        // return{
        //     ...state,
            
        // }
    }

    const purchaseBurgerStart = (state,action)=>{
        return updateObject(state,{loading:true})
        // return {
        //     ...state,
        //     loading:true
        // }
    }

    const purchaseBurgerSuccess = (state,action)=>{
            const newOrder = {
                ...action.orderData,
                id:action.orderId
            }
            return {
                ...state,
                purchased:true,
                loading:false,
                orders:state.orders.concat(newOrder)
            }
    }

    const purchaseBurgerFail = (state,action)=>{
        const errn = action.errorn
            return{
                ...state,
                loading:false,
                action:errn
                
                
            }
    }

    const fetchOrderStart = (state,action)=>{
        return{
            ...state,
            loading:true
        }
    }

    const fetchOrderSuccess = (state,action)=>{
        return{
            ...state,
            orders:action.orders,
            loading:false
        }

    }

    const fetchOrderFail = (state,action)=>{
        return{
            ...state,
            loading:false
        }
    }

const reducer = (state = initialState, action) =>{
    switch(action.type){
        case actionTypes.PURCHASE_INIT:return purchseBurgerInit(state,action)
        case actionTypes.PURCHASE_BURGER_START:return purchaseBurgerStart(state,action)      
        case actionTypes.PURCHASE_BURGER_SUCCESS:return purchaseBurgerSuccess(state,action)  
        case actionTypes.PURCHASE_BURGER_FAIL:return purchaseBurgerFail(state,action)   
        case actionTypes.FETCH_ORDERS_START:return fetchOrderStart(state,action)
        case actionTypes.FETCH_ORDERS_SUCCESS:return fetchOrderSuccess(state,action)
        case actionTypes.FETCH_ORDERS_FAIL:return fetchOrderFail(state,action)    
        default:
            return state
    }
}

export default reducer


